import React from 'react';
import * as ReactBootStrap from "react-bootstrap"
import "./Home.css"
import { BrowserRouter, Route, Link, Switch } from "react-router-dom";

import { FakeServ } from '../FakeDB/FakeServices';

const Home = () => {
    return (
        <div>
            <ReactBootStrap.Carousel>
                <ReactBootStrap.Carousel.Item className="imgSlider">
                    <img
                        className="d-block w-100"
                        src="https://media.gettyimages.com/photos/karate-players-competing-during-the-match-picture-id1182419812?"
                        alt="First slide"
                    />
                    <ReactBootStrap.Carousel.Caption>
                        <h3>First slide label</h3>
                        <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                    </ReactBootStrap.Carousel.Caption>
                </ReactBootStrap.Carousel.Item>
                <ReactBootStrap.Carousel.Item className="imgSlider">
                    <img
                        className="d-block w-100"
                        src="https://stillmed.olympics.com/media/Images/OlympicOrg/News/2017/04/07/2017-04-07-karate-thumbnail.jpg"
                        alt="Second slide"
                    />

                    <ReactBootStrap.Carousel.Caption>
                        <h3>Second slide label</h3>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                    </ReactBootStrap.Carousel.Caption>
                </ReactBootStrap.Carousel.Item>
                <ReactBootStrap.Carousel.Item className="imgSlider">
                    <img
                        className="d-block w-100"
                        src="https://i2-prod.dailyrecord.co.uk/incoming/article13435861.ece/ALTERNATES/s615/0_JS165812180.jpg"
                        alt="Third slide"
                    />

                    <ReactBootStrap.Carousel.Caption>
                        <h3>Third slide label</h3>
                        <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
                    </ReactBootStrap.Carousel.Caption>
                </ReactBootStrap.Carousel.Item>
            </ReactBootStrap.Carousel>

            <br />

            {/* Service Section */}
            <div className="container my-5 ">
                <h3 class="text-center">Services</h3>
                <div class="d-flex justify-content-between">
                    {FakeServ.map(x =>
                        <ReactBootStrap.Card style={{ width: '18rem' }}>
                            <ReactBootStrap.Card.Img variant="top" src={x.img} />
                            <ReactBootStrap.Card.Body>
                                <ReactBootStrap.Card.Title>{x.name}</ReactBootStrap.Card.Title>
                                <ReactBootStrap.Card.Text>
                                    {x.des}
                                </ReactBootStrap.Card.Text>
                                <ReactBootStrap.Card.Text>
                                    {x.price}
                                </ReactBootStrap.Card.Text>
                                <ReactBootStrap.Button variant="warning">Purchase Now!</ReactBootStrap.Button>
                            </ReactBootStrap.Card.Body>
                        </ReactBootStrap.Card>
                    )}
                </div>
            </div>

            <div class="text-center my-4">
                <Link to="/features">
                    <ReactBootStrap.Button variant="warning">See All</ReactBootStrap.Button>
                </Link>
            </div>

        </div>
    );
};

export default Home;