import React from 'react';
import * as ReactBootstrap from "react-bootstrap"

const Navbar = () => {
    return (
        <div>
            <ReactBootstrap.Navbar bg="dark" variant="dark">
                <ReactBootstrap.Container>
                    <ReactBootstrap.Navbar.Brand href="/">Fight Club</ReactBootstrap.Navbar.Brand>
                    <ReactBootstrap.Nav className="me-auto">
                        <ReactBootstrap.Nav.Link href="/">Home</ReactBootstrap.Nav.Link>
                        <ReactBootstrap.Nav.Link href="/features">Services</ReactBootstrap.Nav.Link>
                        <ReactBootstrap.Nav.Link href="/aboutus">About Us</ReactBootstrap.Nav.Link>
                        <ReactBootstrap.Nav.Link href="/contactus">Contact Us</ReactBootstrap.Nav.Link>
                        {/* <ReactBootstrap.Nav.Link href="/err">Error</ReactBootstrap.Nav.Link> */}
                    </ReactBootstrap.Nav>
                </ReactBootstrap.Container>
            </ReactBootstrap.Navbar>

        </div>
    );
};

export default Navbar;